import { RouteInfo } from '../types';
import View from './view'; 

export default class Router {
    // Router class ëª©ì  : hashê°€ ë°”ë€Œì—ˆì„ë•Œ í•´ë‹¹í•˜ëŠ” í˜ì´ì§€ë¥¼ ë³´ì—¬ì¤€ë‹¤.
    private routeTable : RouteInfo[]; // ìµœì´ˆ ì‹œì‘ì‹œì— pathì™€ page(view) ì €ì¥ë¨.
    private defaultRoute : RouteInfo | null; // ìµœì´ˆ ì‹œì‘ì‹œì— NewsFeedView ì €ì¥ë¨.

    constructor() {
        const routePath = location.hash;
        // í•´ì‹œì˜ ë³€ê²½ì„ ê°ì§€í•˜ê³  ë³€ê²½ë˜ë©´ í•¨ìˆ˜ ì‹¤í–‰.
        // window.addEventListenerì—ì„œì˜ this contextëŠ” Router ì¸ìŠ¤í„´ìŠ¤ê°€ ì•„ë‹ˆê³  í•¨ìˆ˜ë¥¼ ì‹¤í–‰í•˜ëŠ” ì£¼ì²´ì¸ ì´ë²¤íŠ¸ê°€ ë¨ 
        window.addEventListener('hashchange', this.route.bind(this));
        this.routeTable = [];
        this.defaultRoute = null;
    }

    setDefaultPage = (page : View) : void => {
        this.defaultRoute = {
            path : '',
            page : page
        }
    }

    addRoutePath = (path : string, page : View) : void => {
        this.routeTable.push({
            path : path, // ë‘˜ì´ ê°™ìœ¼ë‹ˆê¹Œ ê·¸ëƒ¥ pathë¼ê³ ë§Œ ì ì–´ë„ ë¨
            page : page,
        })
    }

    route = () : void => {
        const routePath = location.hash;
        // #ë§Œ ìˆìœ¼ë©´ ê·¸ëƒ¥ ë¹ˆ ë¬¸ìì—´ ''ë¡œ ë‚˜ì˜¨ë‹¤.
        if(routePath == '' && this.defaultRoute) {
            // ë””í´íŠ¸ í˜ì´ì§€ì¸ NewsFeedViewë¡œ ì´ë™.
            // renderí•¨ìˆ˜ë¥¼ ì‹¤í–‰ì‹œí‚´ìœ¼ë¡œì¨ updateViewê°€ ì‹¤í–‰ë˜ê³  í˜ì´ì§€ê°€ innerHTHLë¡œ ê·¸ë ¤ì§€ê²Œ ëœë‹¤. 
            this.defaultRoute.page.render();
        }

        for (const routeInfo of this.routeTable) {
            if (routePath.indexOf(routeInfo.path) >= 0) {
                routeInfo.page.render();
                break;
            }
        }
    }
}